import * as React from 'react';
import { useParams } from 'react-router-dom';

import Staff from '@miq/staffjs';
import { useDocumentTitle } from '@miq/hookjs';
import {
  Button,
  HorizontalScrollGridLayout,
  Loading,
  SectionHeader,
  StickyFooterLayout,
  ViewHeaderBackLink,
} from '@miq/componentjs';

import { uuid } from '@miq/utiljs';
import { TProduct, TProductSize, ProductListFilterForm } from '@shopy/storejs';

import { Cart, TCartInstance } from './utils';
import { ItemProductCard, ProductImage } from './components';
import { getCartPath } from './buttons';
import { SharedDataCtx } from '@miq/contextjs';

type TProps = {
  cart: TCartInstance;
  onSuccess: (res: any) => void;
  onError?: (err: any) => void;
};

type TSelect = TProduct & { sizeSlug: string };

export default function CartUpdateItemsView() {
  useDocumentTitle('Add items to cart - Sales');

  const { cartSlug } = useParams();

  //   const { res, setRes } = useRequest(() => Cart({ slug: cartSlug }).retrieve(), {
  //     refreshDeps: [cartSlug],
  //     // onSuccess: ({ data }) => form.setValues(mapData2Values(data)),
  //     onError: ({ response }) => {
  //       // if (response?.status === 404) return navigate('../');
  //     },
  //   });

  //   const { res: productRes, loading } = useProductListRequest({}, { atc: '1' });
  const ctx = React.useContext(SharedDataCtx);
  const { cart: cartData, products = [], reload } = ctx;

  React.useEffect(() => {
    reload();
  }, [cartSlug]);

  const [selected, setSelected] = React.useState<TSelect[]>([]);

  if (cartSlug !== cartData?.slug || !ctx.isLoaded) return <Loading />;

  const cart = Cart(cartData || {});

  const cartSizes = cart?.items?.map(({ size }) => size) || [];

  const addItems = () => {
    const values = selected.map((s) => ({ product_slug: s.meta_slug!, size: s.sizeSlug, quantity: 1 }));

    if (values.length === 0) return;

    cart
      .addItems(values)
      .then((r) => {
        setSelected([]);
        ctx.updateData({ cart: r.data });
        //   props?.onSuccess?.(r);
      })
      .catch((err) => {
        // props?.onError?.(err)
      });
  };

  return (
    <StickyFooterLayout
      style={{ zIndex: 10000 }}
      className="bg-white p-4 rounded"
      header={<CartAddItemViewHeader {...{ cart, selected }} />}
      footer={
        <div className="miq-container center">
          <div className="d-flex justify-content-center bg-white p-2">
            <div>
              <Button disabled={selected.length === 0} onClick={() => addItems()} className="btn-primary fw-bold">
                Ajouter
              </Button>
            </div>
          </div>
        </div>
      }
    >
      <Staff.Section header={<ProductListFilterForm fields={[]} />} className="miq-container center px-1">
        {products.map((product) => {
          const { sizes = [] } = product;

          return sizes
            .filter(({ quantity }) => quantity >= 1)
            .filter((i) => !cartSizes.includes(i.slug))
            .map((size: TProductSize) => {
              const checked = selected.find(({ sizeSlug }) => sizeSlug === size?.slug);
              const isChecked = Boolean(checked);
              return (
                <div
                  className="d-flex align-items-center mb-1 border-1 rounded"
                  key={uuid()}
                  style={{ padding: 6 }}
                  onClick={() => {
                    if (isChecked) setSelected((items) => items.filter(({ sizeSlug }) => sizeSlug != size?.slug));
                    else setSelected([{ ...product, sizeSlug: size.slug }, ...selected]);
                  }}
                >
                  <div className="flex-1">
                    <ItemProductCard {...{ product, size }} />
                  </div>

                  <div style={{ minWidth: 24, marginLeft: 3, marginRight: 6 }}>
                    <input type="checkbox" name="selected" className="me-2" checked={isChecked} onChange={() => {}} />
                  </div>
                </div>
              );
            });
        })}
      </Staff.Section>
    </StickyFooterLayout>
  );
}

const CartAddItemViewHeader = ({ cart, ...props }: TProps & { selected: TSelect[] }) => {
  const { selected = [] } = props;

  return (
    <div className="cart-add-item-view-header miq-container center px-1 pt-2">
      <div className="d-flex align-items-center">
        <div className="me-1">
          <ViewHeaderBackLink to={getCartPath(cart)} title="Annuler" prefix="miq-staff">
            Â«
          </ViewHeaderBackLink>
        </div>

        <SectionHeader
          title={<span className="fw-bold mt-2">Ajouter des produits</span>}
          text="Tap on a product to add to the cart/ Cliquez sur un produit pour l'ajouter au panier"
          // actions={<div onClick={() => setOpen?.(false)}> [ x ]</div>}
        />
      </div>

      <HorizontalScrollGridLayout className="cart-items-preview" style={{ minHeight: 48, paddingBottom: 0 }}>
        {cart?.items?.map((i) => {
          console.log(i.product);

          return (
            <div key={uuid()}>
              <ProductImage product={i.product} {...{ width: 48, height: 48 }} />
            </div>
          );
        })}
        {selected.map((i) => {
          return (
            <div key={uuid()}>
              <ProductImage product={i} {...{ width: 48, height: 48 }} />
            </div>
          );
        })}
      </HorizontalScrollGridLayout>
    </div>
  );
};
