import { getStaffModel, StaffService } from '@miq/staffjs';
import { TPriceData, TProductSize } from '@shopy/storejs';

import { TCustomer } from '../customer';

const timeout = 5000;

export type TAPIOrderItem = {
  slug: string;
  product: {
    // slug: string;
    meta_slug: string;
    name: string;
    name_truncated: string;
    cover: string;
    cover_data: {
      src: string;
      src_mobile: string;
      thumb: string;
      thumb_sq: string;
      alt_text: string;
      caption: string;
    };
    price: TPriceData;
    retail_price: string;
    retail_price_data: TPriceData;
    sale_price: string;
    sale_price_data: TPriceData;
    is_on_sale: boolean;
    is_oos: boolean;
  };
  size: string;
  size_data: TProductSize;
  quantity: number;
};

export type TAPICart = {
  slug?: string;
  customer?: string;
  customer_name?: string;
  products?: string[];
  items?: TAPIOrderItem[];
};

export type TOrderItem = Record<string, any> & {
  order?: string;
  product?: string;
  product_data?: any;
  name?: string;
  price?: string;
  size?: string;
  was_sale?: boolean;
  img?: string;
  quantity?: number;
};

export type TCart = {
  slug?: string;
  customer?: string;
  customer_name?: string;
  customer_data?: TCustomer;
  notes?: string;
  is_paid?: boolean;

  dt_delivery?: string;
  items?: TOrderItem[];
  products?: string[];

  is_placed?: boolean;
  subtotal?: TPriceData;
  total?: TPriceData;

  created?: string;
  updated?: string;
};

class CartService extends StaffService<TCart> {
  get createdDt() {
    return this.data?.created
      ? new Date(this.data.created!).toLocaleDateString(undefined, {
          day: '2-digit',
          weekday: 'short',
          month: 'short',
          year: '2-digit',
        })
      : null;
  }

  get canDestroy(): boolean {
    return this?.data?.products?.length === 0;
  }

  place() {
    return this.post(`${this._path}place/`, {}, { timeout });
  }

  addItems(values: { size: string; quantity: number; product_slug: string }[]) {
    return this.post(`${this._path}products/`, values);
  }

  addItem(prodSlug: string, values: { size: string; quantity: number }) {
    return this.post(`${this._path}product/${prodSlug}/`, values);
  }

  updateItem(prodSlug: string, values: Record<string, any>) {
    return this.post(`${this._path}item/${prodSlug}/`, { ...values }, { timeout });
  }

  destroyItem(itemSlug: string) {
    return this.delete(`${this._path}item/${itemSlug}/`, { timeout });
  }
}

export const Cart = getStaffModel<TCart, CartService>('carts', CartService);

export type TCartInstance = ReturnType<typeof Cart>;
